.landingPageContainer{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    background-color: rgba(0, 0, 0, 0.066);
}

.blogTitleContainer{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100vw;
    height: 8vh;
    position: fixed;
    z-index: 999;
}

.blogTitle{
    text-decoration: underline;
    font-family: 'Archivo Narrow', sans-serif;
    font-size: 3em;
    color: #1F1300;
}

.headingImageContainer{
    height: 100vh;
    width: 100%;
    background-image: url('https://sthamericablogphotos.s3.ap-southeast-2.amazonaws.com/pexels-carolyn-3345082.jpg');
    background-size: cover;
    display: flex;
    justify-content:flex-start;
    align-items: center;
    margin-bottom: 50px;
}

.blogIntroContainer{
    text-align: left;
    height: 25vh;
    max-width: 58vw;
    margin-left: 100px;
}

.tileLinks{
    color: inherit;
    text-decoration: inherit;
}

.blogIntroTitle{
    font-family: 'Cormorant', serif;
    font-size: 10vh;
    color: #1F1300;
}

.blogIntroSubheaderContainer{
    text-align:left;
    width: 50vw;
}

.blogIntroSubheader{
    font-family: 'Cormorant', serif;
    font-size: 3vh;
    color: #1F1300;
}

.blogPostTilesContainer{
    margin: 100px;
    max-width: 1000px;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    border-left: 3px solid #1F1300;
    border-right: 3px solid #1F1300;
}